export default {
	data() {
		return {
			info:"",
			hisList:[],
			goodsList:[],
		}
	},
	methods: {
		//清空历史
		clear(){
			this.request({
				url:this.config.url+"del/keyword",
				method:"post",
				data:{},
				success: (res) => {
					if(res.data.code==1){
						uni.showToast({
							title:res.data.info
						})
						this.goodsList=[]
					}
				}
			})
			
		},
		searchInfo(val){
	        
			var keyword;
			if(val){
				keyword=val
			}else{
				keyword=this.info;
			}
		
			console.log("ke", keyword)
			uni.navigateTo({
				url:"/pages/goods/goods-list?keyword="+keyword
			})
		}
	},
	mounted() {

			this.request({
				method:"post",
				url:this.config.url+"keyword",
				data:{},
				success:res =>{
					console.log(res)
					if(res.data.code==1){
						this.hisList=res.data.data.keyword;
						this.goodsList=res.data.data.history;
					}
				}
			})
		
	}
}
